<template>
  <div class="app-container">
    <nav class="navbar">
      <router-link
        to="/"
        class="logo"
      >
        SimpleSwap
      </router-link>
      <div class="wallet-info">
        <span v-if="account">已连接: {{ shortAddress(account) }}</span>
        <button
          v-else
          @click="connectWallet"
        >
          连接钱包
        </button>
      </div>
    </nav>

    <div
      v-if="globalError"
      class="global-error"
    >
      {{ globalError }}
      <button @click="globalError = ''">
        ×
      </button>
    </div>

    <router-view />
  </div>
</template>


<script setup>
import { ref, onMounted } from 'vue';
import { getAccount, initWeb3 } from './utils/web3';

const account = ref('');
const globalError = ref('');

const shortAddress = (addr) => `${addr.slice(0, 6)}...${addr.slice(-4)}`;

async function connectWallet() {
  try {
    await initWeb3();
    account.value = await getAccount();
    console.log('[App] 钱包连接成功，触发事件'); // 调试日志

    // 触发自定义事件（全小写命名）
    const event = new Event('wallet-connected');
    window.dispatchEvent(event);
  } catch (error) {
    globalError.value = error.message;
  }
}

onMounted(async () => {
  try {
    await connectWallet()
    // 自动连接逻辑（不要求授权）
    if (window.ethereum?.selectedAddress) {
      await initWeb3(false); // 静默初始化
      account.value = await getAccount();
      window.dispatchEvent(new Event('wallet-connected'));
    }

    // 账户变化监听
    window.ethereum?.on('accountsChanged', async (accounts) => {
      account.value = accounts[0] || '';
      window.dispatchEvent(new Event('account-changed'));
    });
  } catch (e) {
    console.log('自动连接失败:', e);
  }
});
</script>

