  border-radius: 8px;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
  text-decoration: none;
  color: #333;
}

.wallet-info button {
  padding: 8px 16px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.global-error {
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 1rem;
  background: #ffe3e3;
  color: #c00;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}
</style>