// src/utils/web3.js
import Web3 from 'web3'

let web3 = null;

// 自动初始化（无用户交互）
function autoInitWeb3() {
    if (window.ethereum) {
        web3 = new Web3(window.ethereum);
    } else if (window.web3) {
        web3 = new Web3(window.web3.currentProvider);
    }
}
// 立即执行自动初始化
autoInitWeb3();

export async function initWeb3(requireAuth = true) {
    if (!web3) throw new Error('请安装MetaMask钱包');

    try {
        if (window.ethereum && requireAuth) {
            await window.ethereum.request({ method: 'eth_requestAccounts' });
        }
        return true;
    } catch (error) {
        throw new Error(requireAuth ? '用户拒绝连接钱包' : '自动连接失败');
    }
}

export function getWeb3() {
    if (!web3) throw new Error('Web3未初始化，请先链接钱包');
    return web3;
}

export async function getAccount() {
    if (!web3) return '';
    const accounts = await web3.eth.getAccounts();
    return accounts[0] || '';
}